// @flow
import * as React from 'react';
import { InView } from 'react-intersection-observer';

export default class TransitionBlock extends React.Component {
  static defaultProps = {
    from: 'bottom',
    delay: 0,
    transition: 'ease-out',
    duration: 1250,
    offset: '100px',
    animateOpacity: true,
    triggerOnce: false,
    threshold: 0,
		display: 'inline-block',
		opacityDelay: 0,
  }

  constructor() {
    super();
    this.state = { playAnim: false };
  }

  onInViewChange = inView => {
    this.setState({ playAnim: inView });
  }

  getOpacity = () => {
    const { animateOpacity } = this.props;
    const { playAnim } = this.state;

    if (playAnim || !animateOpacity) {
      return '1';
    }

    return '0';
  }

  getTransform = () => {
    const { offset, from, perspective, perspectiveX, perspectiveY } = this.props;
    const { playAnim } = this.state;

    let transform;

    if (playAnim) {
      transform = 'translate3d(0,0,0) rotateX(0) rotateY(0)';
    } else {
      switch (from) {
        case 'top':
          transform = `translate3d(0,-${offset},0)`;
          break
        case 'bottom':
          transform = `translate3d(0,${offset},0)`;
          break
        case 'left':
          transform = `translate3d(-${offset},0,0)`;
          break
        case 'right':
          transform = `translate3d(${offset},0,0)`;
          break
        default:
          transform = `translate3d(0,${offset},0)`;
          break
      }

      // set perspective CSS 3d
      if(perspective) {
        transform = `${transform} rotateX(${perspectiveX}deg) rotateY(${perspectiveY}deg)`
      }

    }


    return transform
  }

  getTransition = () => {
    const { delay, duration, transition, opacityDelay } = this.props;
    return `opacity ${duration}ms ease ${opacityDelay ? opacityDelay : delay}ms, transform ${duration}ms ${transition} ${delay}ms`;
  }

  getStyles = () => {
    const { display, underline } = this.props;
    const transition = this.getTransition();
    const transform = this.getTransform();
    const opacity = this.getOpacity();

    return {
      display,
      transform,
      transition,
      opacity,
      width: '100%',
      textDecoration: underline ? 'underline' : 'none',
    }
  }

  render() {
    const { triggerOnce, threshold, children } = this.props;
    const inlineStyle = this.getStyles();

    return (
      <InView
        threshold={threshold}
        triggerOnce={triggerOnce}
        onChange={this.onInViewChange}
      >
        <div
          ref={r => {
            this.elRef = r
          }}
        >
          <span style={inlineStyle}>{children}</span>
        </div>
      </InView>
    )
  }
}
