import React from 'react';
import TextareaAutosize from 'react-textarea-autosize';
import TransitionBlock from '../TransitionBlock'
import { formatBezierCurveCSS } from '../../utils';
import './wordBlock.scss';

export default class WordBlock extends React.Component {
	constructor(props) {
		super(props)
		this.state = { lines: [] };
		this.editTextArea = null;
	}

	componentDidMount() {
		this.generateWords();
	}

	onKeyDown = (e) => {
		switch(e.key) {
			case 'Escape': 
				this.onAccept();
				break;
			default: 
				return;
		}
	}

	setTextAreaRef = (ref) => {
		this.editTextArea = ref;
	}

	onFocus = () => {
		this.onEdit();
	}

	onBlur = () => {
		this.onAccept();
	}	

	onAccept = () => {
		const {onEditModeChange} = this.props;
		this.generateWords();
		onEditModeChange(false);
	}

	onEdit = () => {
		const {onEditModeChange, staticView} = this.props;
		if(staticView) return; // In static mode we don't want the edit option

		setTimeout(() => {
			if(this.editTextArea) {
				this.editTextArea.focus();
			}
		}, 200);

		onEditModeChange(true);
	}

	generateWords = () => {
		const { copy } = this.props;
		const lines = copy.split('\n');
		const wordLines = lines.map(line => {
			return line.split(" "); // separate by word 
		});

		this.setState({ lines: wordLines });
	}

	render() {
		const { copy, onChange, fontOptions, transitionOptions, inEditMode, staticView } = this.props;
		const { lines } = this.state;
		const bezierCurve = formatBezierCurveCSS(transitionOptions.bezierCurve);

		return (
			<div className="multiline-editor">

				<div className={`multiline-editor__textarea ${inEditMode ? '' : 'hidden'}`}>
					<LineWrapper {...this.props}>
						<TextareaAutosize
							inputRef={ref => this.setTextAreaRef(ref)}
							value={copy}
							placeholder="Enter your wittiest copy."
							onChange={onChange}
							onFocus={this.onFocus}
							onBlur={this.onBlur}
							onKeyDown={this.onKeyDown}
						/>
					</LineWrapper>
				</div>

				{!inEditMode && <div className={`wordview__wrapper`}>
					{lines.map((line, i) => {

						return (
							<LineWrapper key={i} {...this.props} onClick={this.onEdit}>
								{line.map((word, j) => {

									return (
										<WordWrapper key={j} transitionOptions={transitionOptions}>

											<TransitionBlock
												{...transitionOptions}
												triggerOnce
												from={transitionOptions.direction}
												delay={(i * transitionOptions.multilineOffsetDelay) + (j * transitionOptions.wordOffsetDelay)}
												animateOpacity={transitionOptions.opacity}
												duration={transitionOptions.duration}
												offset={`${transitionOptions.offset}px`}
												transition={bezierCurve}
												opacityDelay={(i * transitionOptions.opacityDelay) + (j * transitionOptions.opacityDelay)}
												underline={fontOptions.underline}
											>
												{word}
											</TransitionBlock>
										
										</WordWrapper>
									);
								})}
							</LineWrapper>);
					}
					)}
				</div>

				}

			</div>
		)
	}
}

class LineWrapper extends React.PureComponent {
	render() {
		const { transitionOptions, fontOptions, onClick, staticView } = this.props;

		return <div onClick={onClick}
			style={{
				display: 'block',
				fontFamily: fontOptions.customFont ? `'${fontOptions.customFont}'` : fontOptions.family,
				fontWeight: fontOptions.fontWeight,
				fontSize: `${fontOptions.size}px`,
				color: staticView ? 'white' : fontOptions.color,
				lineHeight: `${fontOptions.lineHeight}px`,
				letterSpacing: `${fontOptions.letterSpacing}em`,
				maxWidth: `${fontOptions.maxWidth}px`,
				width: `${fontOptions.maxWidth}px`,
				overflow: `${transitionOptions.multilineMasking ? 'hidden' : 'visible'}`,
				pointerEvents: staticView ? 'none' : 'normal'
			}}
		>
			{this.props.children}
		</div>
	}
};


class WordWrapper extends React.PureComponent {
	render() {
		const { transitionOptions } = this.props;

		return <div
			style={{
				display: 'inline-block',
				marginRight: `${transitionOptions.characterWordSpacing}px`,
			}}
		>
			{this.props.children}
		</div>
	}
};